const replaceArr = replace.item
export const getRandomSpot = async (url) => {
  try {
    const res = await axios.get(url)

    if(res.data?.response === undefined ||res.data?.response ===null ){
      return makeRandom(replaceArr)
    }
    const itemList = res.data.response.body.items.item;
    const result = makeRandom(itemList);
    // console.log(result);
    return result;
  } catch (err) {
    console.log("에러", err);
    return []
    // throw err; 
  }
};

